"use client";
import { useEffect, useRef, useLayoutEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

const Statement = () => {
  const statementRef = useRef(null);
  const statementSectionRef = useRef(null);
  const hrRef = useRef(null);
  const textContainersRef = useRef([]);
  const columnOneRef = useRef(null);
  const columnTwoRef = useRef(null);
  const columnOneContainersRef = useRef([]);
  const columnTwoContainersRef = useRef([]);

  // Function to create masked text animation
  const createMaskedTextAnimation = (
    element,
    text,
    containersRef,
    indent = false
  ) => {
    if (!element) return;

    // Split text into words
    const words = text.split(" ");
    element.innerHTML = "";

    // Create wrapper div
    const textWrapper = document.createElement("div");
    textWrapper.className = "text-wrapper";
    textWrapper.style.display = "flex";
    textWrapper.style.flexWrap = "wrap";
    textWrapper.style.alignItems = "flex-start";
    textWrapper.style.justifyContent = "flex-start";

    // Add indent if needed
    if (indent) {
      const indentDiv = document.createElement("div");
      indentDiv.style.width = "4em";
      indentDiv.style.display = "inline-block";
      indentDiv.style.height = "1px";
      textWrapper.appendChild(indentDiv);
    }

    // Create word containers and spans
    words.forEach((word, index) => {
      const container = document.createElement("div");
      container.style.display = "inline-block";
      container.style.overflow = "hidden";
      container.style.verticalAlign = "top";
      container.style.position = "relative";

      const wordSpan = document.createElement("span");
      wordSpan.innerHTML = word;
      wordSpan.style.display = "inline-block";
      wordSpan.style.transform = "translateY(100%)";

      container.appendChild(wordSpan);
      containersRef.current.push({ container, wordSpan });

      textWrapper.appendChild(container);

      if (index < words.length - 1) {
        const space = document.createElement("span");
        space.innerHTML = "\u00A0";
        textWrapper.appendChild(space);
      }
    });

    element.appendChild(textWrapper);
  };

  useLayoutEffect(() => {
    gsap.registerPlugin(ScrollTrigger);
    textContainersRef.current = [];
    columnOneContainersRef.current = [];
    columnTwoContainersRef.current = [];

    const ctx = gsap.context(() => {
      // Main statement animation
      createMaskedTextAnimation(
        statementRef.current,
        statementRef.current.textContent,
        textContainersRef,
        true
      );

      // Column one animation
      createMaskedTextAnimation(
        columnOneRef.current,
        columnOneRef.current.textContent,
        columnOneContainersRef,
        false
      );

      // Column two animation
      createMaskedTextAnimation(
        columnTwoRef.current,
        columnTwoRef.current.textContent,
        columnTwoContainersRef,
        false
      );

      // Animate the main statement words
      textContainersRef.current.forEach(({ wordSpan }, index) => {
        gsap.to(wordSpan, {
          y: 0,
          duration: 0.5,
          ease: "power2.out",
          delay: index * 0.05,
          scrollTrigger: {
            trigger: statementRef.current,
            start: "top 80%",
            end: "bottom 90%",
          },
        });
      });

      // Animate the HR
      gsap.fromTo(
        hrRef.current,
        { width: "0%" },
        {
          width: "100%",
          duration: 1,
          ease: "power2.out",
          scrollTrigger: {
            trigger: hrRef.current,
            start: "top 90%",
            end: "bottom 90%",
          },
        }
      );

      // Animate column one words
      columnOneContainersRef.current.forEach(({ wordSpan }, index) => {
        gsap.to(wordSpan, {
          y: 0,
          duration: 0.5,
          ease: "power2.out",
          delay: index * 0.05,
          scrollTrigger: {
            trigger: columnOneRef.current,
            start: "top 90%",
            end: "bottom 90%",
          },
        });
      });

      // Animate column two words
      columnTwoContainersRef.current.forEach(({ wordSpan }, index) => {
        gsap.to(wordSpan, {
          y: 0,
          duration: 0.5,
          ease: "power2.out",
          delay: index * 0.05,
          scrollTrigger: {
            trigger: columnTwoRef.current,
            start: "top 90%",
            end: "bottom 90%",
          },
        });
      });
    }, statementSectionRef);

    return () => ctx.revert();
  }, []);

  return (
    <div
      ref={statementSectionRef}
      className="bg-black text-white py-16 px-4 md:px-8 lg:px-16 statement-section w-full"
    >
      <div className="max-w-7xl mx-auto">
        <h1
          ref={statementRef}
          className="text-4xl text-white md:text-5xl lg:text-6xl font-light leading-tight tracking-tighter mb-12"
        >
          We blend the power of strategy, design, and performance marketing to
          transform founders' visions into remarkable brands. See{" "}
          <a href="#services" className="underline">
            our services
          </a>
          .
        </h1>
      </div>

      {/* Animated horizontal rule */}
      <div
        ref={hrRef}
        className="w-0 border-gray-50/50 border-b h-px max-w-7xl mx-auto"
      ></div>

      {/* Two-column section */}
      <div className="max-w-7xl mx-auto mt-16 grid grid-cols-1 md:grid-cols-2 gap-8">
        <div className="column">
          <h3 ref={columnOneRef} className="text-2xl font-light mb-6">
            Our approach combines strategic thinking with creative execution to
            deliver brands that resonate with your audience and drive measurable
            results.
          </h3>
        </div>
        <div className="column">
          <p ref={columnTwoRef} className="text-lg font-light">
            We partner with ambitious founders who understand that exceptional
            branding is not just about aesthetics, but about creating meaningful
            connections that drive business growth.
          </p>
        </div>
      </div>
    </div>
  );
};

export default Statement;
