"use client";
import { useRef, useState, useEffect } from "react";
import { X, Volume2, VolumeX, Play, Pause } from "lucide-react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

const Showreel = () => {
  gsap.registerPlugin(ScrollTrigger);

  // State variables
  const [isOpen, setIsOpen] = useState(false);
  const [isMuted, setIsMuted] = useState(true);
  const [isPlaying, setIsPlaying] = useState(false);
  const [isHovering, setIsHovering] = useState(false);

  // Refs
  const sectionRef = useRef(null);
  const videoContainerRef = useRef(null);
  const showreelCursorRef = useRef(null);
  const modalCursorRef = useRef(null);
  const modalRef = useRef(null);
  const videoRef = useRef(null);
  const overlayRef = useRef(null);
  const timelineRef = useRef(null);

  // Cursor position tracking
  const cursor = useRef({
    x: 0,
    y: 0,
    targetX: 0,
    targetY: 0,
  });

  // Video playback functions
  const playVideo = () => {
    if (videoRef.current) {
      videoRef.current
        .play()
        .then(() => setIsPlaying(true))
        .catch((error) => {
          console.error("Playback error:", error);
          if (!isMuted) {
            setIsMuted(true);
            videoRef.current.muted = true;
            videoRef.current.play().then(() => setIsPlaying(true));
          }
        });
    }
  };

  const openModal = () => setIsOpen(true);

  const closeModal = () => {
    if (timelineRef.current) {
      timelineRef.current.reverse().then(() => {
        setIsOpen(false);
        if (videoRef.current) {
          videoRef.current.pause();
          setIsPlaying(false);
        }
      });
    }
  };

  const toggleMute = (e) => {
    e.stopPropagation(); // Prevent closing the modal
    if (videoRef.current) {
      const newMuted = !isMuted;
      videoRef.current.muted = newMuted;
      setIsMuted(newMuted);
      if (!newMuted && !isPlaying) playVideo();
    }
  };

  const togglePlayPause = (e) => {
    e.stopPropagation(); // Prevent closing the modal
    if (videoRef.current) {
      if (isPlaying) {
        videoRef.current.pause();
        setIsPlaying(false);
      } else {
        playVideo();
      }
    }
  };

  // Initialize scroll animation and cursor followers
  useEffect(() => {
    // Scroll Animation for showreel
    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: sectionRef.current,
        start: "top bottom",
        end: "center center",
        scrub: 1,
      },
    });

    gsap.set(videoContainerRef.current, {
      scale: 0.8,
      opacity: 0,
    });

    tl.to(videoContainerRef.current, {
      scale: 1,
      opacity: 1,
      ease: "power2.out",
    });

    // Cursor following functionality
    const showreelFollower = showreelCursorRef.current;
    const modalFollower = modalCursorRef.current;

    gsap.set([showreelFollower, modalFollower], {
      xPercent: -50,
      yPercent: -50,
      opacity: 0,
      scale: 0.5,
    });

    const onMouseMove = (e) => {
      cursor.current.targetX = e.clientX;
      cursor.current.targetY = e.clientY;
    };

    const followCursor = () => {
      cursor.current.x += (cursor.current.targetX - cursor.current.x) * 0.1;
      cursor.current.y += (cursor.current.targetY - cursor.current.y) * 0.1;

      if (showreelFollower && modalFollower) {
        gsap.set([showreelFollower, modalFollower], {
          x: cursor.current.x,
          y: cursor.current.y,
        });
      }

      requestAnimationFrame(followCursor);
    };

    followCursor();
    document.addEventListener("mousemove", onMouseMove);

    // Escape key to close modal
    const handleEsc = (e) => {
      if (e.key === "Escape" && isOpen) closeModal();
    };

    window.addEventListener("keydown", handleEsc);

    return () => {
      document.removeEventListener("mousemove", onMouseMove);
      window.removeEventListener("keydown", handleEsc);
    };
  }, [isOpen]);

  // Modal animation when opened
  useEffect(() => {
    if (isOpen) {
      const tl = gsap.timeline();

      // Start hidden
      gsap.set([overlayRef.current, modalRef.current], { opacity: 0 });
      gsap.set(modalRef.current, { scale: 0.95, rotate: 5 });

      tl.to(overlayRef.current, {
        opacity: 1,
        duration: 0.3,
        ease: "power2.out",
      })
        .to(
          modalRef.current,
          {
            opacity: 1,
            scale: 1,
            rotate: 0,
            duration: 0.6,
            ease: "power3.out",
          },
          "-=0.2"
        )
        .call(() => {
          if (videoRef.current) {
            videoRef.current.style.visibility = "visible";
            playVideo();
          }
        });

      timelineRef.current = tl;

      // Show the modal cursor follower
      gsap.to(modalCursorRef.current, {
        opacity: 1,
        scale: 1,
        duration: 0.3,
        ease: "power3.out",
      });
    } else {
      // Hide the modal cursor follower when modal is closed
      if (modalCursorRef.current) {
        gsap.to(modalCursorRef.current, {
          opacity: 0,
          scale: 0.5,
          duration: 0.3,
          ease: "power3.out",
        });
      }
    }
  }, [isOpen]);

  // Showreel hover state management
  const handleShowreelMouseEnter = () => {
    if (!isOpen) {
      setIsHovering(true);
      gsap.to(showreelCursorRef.current, {
        opacity: 1,
        scale: 1,
        duration: 0.3,
        ease: "power3.out",
      });
    }
  };

  const handleShowreelMouseLeave = () => {
    setIsHovering(false);
    gsap.to(showreelCursorRef.current, {
      opacity: 0,
      scale: 0.5,
      duration: 0.3,
      ease: "power3.out",
    });
  };

  return (
    <>
      {/* Play Reel cursor follower */}
      <div
        ref={showreelCursorRef}
        className="pointer-events-none fixed top-0 left-0 w-auto h-20 flex items-center justify-center gap-2 bg-opacity-20 rounded-full z-50"
        style={{
          opacity: 0,
          transform: "translate(-50%, -50%)",
        }}
      >
        <div className="w-6 h-6 flex items-center justify-center">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
            fill="white"
            className="w-full h-full"
          >
            <path d="M8 5v14l11-7z" />
          </svg>
        </div>
        <p className="text-white">Play Reel</p>
      </div>

      {/* Close Reel cursor follower */}
      <div
        ref={modalCursorRef}
        className="pointer-events-none fixed top-0 left-0 w-auto h-20 flex items-center justify-center gap-2 bg-opacity-20 rounded-full z-[9999]"
        style={{
          opacity: 0,
          transform: "translate(-50%, -50%)",
        }}
      >
        <div className="w-6 h-6 flex items-center justify-center">
          <X size={20} color="white" />
        </div>
        <p className="text-white">Close Reel</p>
      </div>

      {/* Showreel Section */}
      <section
        ref={sectionRef}
        className="w-full h-[800px] flex items-center justify-center relative overflow-hidden bg-black"
      >
        {/* Video Container */}
        <div
          ref={videoContainerRef}
          onMouseEnter={handleShowreelMouseEnter}
          onMouseLeave={handleShowreelMouseLeave}
          onClick={openModal}
          className="w-full h-full flex items-center justify-center cursor-pointer relative"
        >
          <video
            src="/reel.mp4"
            draggable={false}
            autoPlay={true}
            preload="none"
            playsInline={true}
            muted
            className="w-full h-full object-cover"
          ></video>
          <div className="absolute inset-0 flex items-center justify-center">
            <h1 className="text-7xl text-white">Show Reel</h1>
          </div>
        </div>
      </section>

      {/* Video Modal */}
      {isOpen && (
        <div
          ref={overlayRef}
          className="fixed inset-0 bg-black bg-opacity-80 z-50 flex items-center justify-center"
          onClick={closeModal}
        >
          <div
            ref={modalRef}
            className="relative w-full max-w-5xl aspect-video bg-black overflow-hidden rounded shadow-xl"
          >
            <video
              ref={videoRef}
              className="w-full h-full object-cover"
              playsInline
              loop
              muted={isMuted}
              style={{
                visibility: "hidden",
              }}
            >
              <source src="/reel.mp4" type="video/mp4" />
            </video>

            <div className="absolute bottom-4 left-4 flex gap-2">
              <button
                onClick={toggleMute}
                className="bg-white bg-opacity-20 hover:bg-opacity-30 rounded-full p-2 text-white transition-all"
              >
                {isMuted ? <VolumeX size={20} /> : <Volume2 size={20} />}
              </button>

              <button
                onClick={togglePlayPause}
                className="bg-white bg-opacity-20 hover:bg-opacity-30 rounded-full p-2 text-white transition-all"
              >
                {isPlaying ? <Pause size={20} /> : <Play size={20} />}
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Showreel;
