"use client";
import { useRef, useState, useEffect } from "react";
import { X, Volume2, VolumeX, Play, Pause } from "lucide-react";
import { gsap } from "gsap";

const VideoModal = ({ videoSrc = "/reel.mp4" }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isMuted, setIsMuted] = useState(true);
  const [isPlaying, setIsPlaying] = useState(false);

  const modalRef = useRef(null);
  const videoRef = useRef(null);
  const overlayRef = useRef(null);
  const timelineRef = useRef(null); // store gsap timeline

  const playVideo = () => {
    if (videoRef.current) {
      videoRef.current
        .play()
        .then(() => setIsPlaying(true))
        .catch((error) => {
          console.error("Playback error:", error);
          if (!isMuted) {
            setIsMuted(true);
            videoRef.current.muted = true;
            videoRef.current.play().then(() => setIsPlaying(true));
          }
        });
    }
  };

  const openModal = () => setIsOpen(true);

  const closeModal = () => {
    if (timelineRef.current) {
      timelineRef.current.reverse().then(() => {
        setIsOpen(false);
        if (videoRef.current) {
          videoRef.current.pause();
          setIsPlaying(false);
        }
      });
    }
  };

  const toggleMute = () => {
    if (videoRef.current) {
      const newMuted = !isMuted;
      videoRef.current.muted = newMuted;
      setIsMuted(newMuted);
      if (!newMuted && !isPlaying) playVideo();
    }
  };

  const togglePlayPause = () => {
    if (videoRef.current) {
      if (isPlaying) {
        videoRef.current.pause();
        setIsPlaying(false);
      } else {
        playVideo();
      }
    }
  };

  useEffect(() => {
    const handleEsc = (e) => {
      if (e.key === "Escape" && isOpen) closeModal();
    };
    window.addEventListener("keydown", handleEsc);
    return () => window.removeEventListener("keydown", handleEsc);
  }, [isOpen]);

  useEffect(() => {
    if (isOpen) {
      const tl = gsap.timeline();

      // Start hidden
      gsap.set([overlayRef.current, modalRef.current], { opacity: 0 });
      gsap.set(modalRef.current, { scale: 0.95, rotate: 5 });

      tl.to(overlayRef.current, {
        opacity: 1,
        duration: 0.3,
        ease: "power2.out",
      })
        .to(
          modalRef.current,
          {
            opacity: 1,
            scale: 1,
            rotate: 0,
            duration: 0.6,
            ease: "power3.out",
          },
          "-=0.2"
        )
        .call(() => {
          if (videoRef.current) {
            videoRef.current.style.visibility = "visible";
            playVideo();
          }
        });

      timelineRef.current = tl;
    }
  }, [isOpen]);

  return (
    <>
      <button
        onClick={openModal}
        className="px-6 py-3 bg-black text-white rounded hover:bg-gray-800 transition-colors"
      >
        Watch Video
      </button>

      {isOpen && (
        <div
          ref={overlayRef}
          className="fixed inset-0 bg-black bg-opacity-80 z-50 flex items-center justify-center"
          onClick={closeModal}
        >
          <div
            ref={modalRef}
            className="relative w-full max-w-5xl aspect-video bg-black overflow-hidden rounded shadow-xl"
            onClick={(e) => e.stopPropagation()}
          >
            <video
              ref={videoRef}
              className="w-full h-full object-cover"
              playsInline
              loop
              muted={isMuted}
              style={{
                visibility: "hidden",
              }}
            >
              <source src={videoSrc} type="video/mp4" />
            </video>

            <div className="absolute bottom-4 left-4 flex gap-2">
              <button
                onClick={toggleMute}
                className="bg-white bg-opacity-20 hover:bg-opacity-30 rounded-full p-2 text-white transition-all"
              >
                {isMuted ? <VolumeX size={20} /> : <Volume2 size={20} />}
              </button>

              <button
                onClick={togglePlayPause}
                className="bg-white bg-opacity-20 hover:bg-opacity-30 rounded-full p-2 text-white transition-all"
              >
                {isPlaying ? <Pause size={20} /> : <Play size={20} />}
              </button>
            </div>

            <button
              onClick={closeModal}
              className="absolute top-4 right-4 text-white flex items-center gap-2 opacity-80 hover:opacity-100 transition-opacity"
            >
              <X size={20} />
              <span>Close Reel</span>
            </button>
          </div>
        </div>
      )}
    </>
  );
};

export default VideoModal;
